import React, { useReducer, useState } from "react";
import { Button, Container, Grid, TextField, Typography } from "@material-ui/core";
import { useHistory } from "react-router";
import { NavLink } from "react-router-dom";

export default function Login() {
  const history = useHistory();
  const [form, updateForm] = useReducer(({ name, value }, state) => ({ ...state, [name]: value }), {
    email: "",
    password: "",
  });
  const [loginFailed] = useState(false);

  async function submit(e) {
    e.preventDefault();
    const { email, password } = form;
    sessionStorage.setItem("email", email);
    sessionStorage.setItem("password", password);
    history.push("/");
    // const response = await fetch("https://realm.mongodb.com/api/client/v2.0/app/application-0-eomix/graphql", {
    //   method: "POST",
    //   headers: {
    //     email,
    //     password,
    //     "Content-Type": "application/json",
    //   },
    //   // body: JSON.stringify({
    //   //   query: `
    //   //     query ($email: string!, password: string!) {
    //   //       users(where: {email: $email, password: $password}) {
    //   //         _id,
    //   //         email
    //   //       }
    //   //     }
    //   //   `,
    //   //   variables: form,
    //   // }),
    // }).then((res) => res.json());
  }

  return (
    <form onSubmit={submit}>
      <Container component="main" maxWidth="xs">
        <div style={{ marginTop: "80px", display: "flex", flexDirection: "column", alignItems: "center" }}>
          <Typography component="h1" variant="h5">
            Login
          </Typography>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
            onChange={(e) => updateForm({ name: "email", value: e.target.value })}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            onChange={(e) => updateForm({ name: "password", value: e.target.value })}
          />
          {loginFailed && <Typography component="p">Login Failed</Typography>}
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            style={{ marginTop: "20px" }}
            onClick={submit}
          >
            Submit
          </Button>
          <Grid container style={{ marginTop: "10px" }}>
            <Grid item>
              or{" "}
              <NavLink to="/create-account" variant="body2">
                Create Account
              </NavLink>
            </Grid>
          </Grid>
        </div>
      </Container>
    </form>
  );
}
